.restaurant-wrapper {
    .restaurant-img {
        height       : 178px;
        overflow     : hidden;
        border-radius: 10px;

        .restaurant-view {
            position       : absolute;
            right          : 15px;
            background     : #00000087;
            padding        : 2px 12px;
            border-radius  : 25px;
            top            : 20px;
            display        : flex;
            align-items    : center;
            justify-content: center;
            font-size      : 14px;
        }
    }

    .restaurant-details {
        max-width    : calc(100% - 30px);
        margin       : auto;
        padding      : 1rem 0.75rem;
        background   : #fff;
        border-radius: 10px;
        box-shadow   : 0px 5px 12px 0px #00000014;
        position     : relative;
        margin-top   : -3rem;

        .location-icon {
            top: -2px
        }

        .foodtypes-wrap {
            .food-types {
                background   : #F6F6F6;
                width        : 36px;
                height       : 36px;
                border-radius: 50px;
            }
        }
    }
}
